import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Container } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { getIconForHardSkill } from '../../mock/aboutMe/hardSkillsData';
import '../../style/pages/_hardSkills.scss';

const generateUniqueKey = () => {
  return Math.random().toString(36).substring(7);
};

const HardSkills = ({ hardSkills }) => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 769);
    };

    handleResize(); // Initial check
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <Container fluid className="hardskills-page-container">
      <Row className={`hardskills-row ${isMobile ? 'mobile-layout' : ''}`}>
        {isMobile ? (
          Object.keys(hardSkills).map((category) => (
            <Col key={category} className={`hardskill-column ${isMobile ? 'mobile-layout' : ''}`}>
              <div className="hardskill-card">
                <h2 className="hardskills-category-heading">{category}</h2>
                {hardSkills[category].map((hardskill) => (
                  <div className="hardskill-item" key={generateUniqueKey()}>
                    <FontAwesomeIcon icon={getIconForHardSkill(hardskill)} className="icon" />
                    <span className="hardskill-text">{hardskill}</span>
                  </div>
                ))}
              </div>
            </Col>
          ))
        ) : (
          <>
            <Col lg={4} className="hardskill-column">
              <div className="skill-card large-category">
                <h2 className="hardskills-category-heading">{Object.keys(hardSkills)[0]}</h2>
                {hardSkills[Object.keys(hardSkills)[0]].map((hardskill) => (
                  <div className="hardskill-item" key={generateUniqueKey()}>
                    <FontAwesomeIcon icon={getIconForHardSkill(hardskill)} className="icon" />
                    <span className="skill-text">{hardskill}</span>
                  </div>
                ))}
              </div>
            </Col>
            <Col lg={8} className="other-categories">
              <Row className="h-100">
                {Object.keys(hardSkills)
                  .slice(1, 5)
                  .map((category) => (
                    <Col xs={6} className="hardskill-column small-category" key={category}>
                      <div className="skill-card">
                        <h2 className="hardskills-category-heading">{category}</h2>
                        {hardSkills[category].map((hardskill) => (
                          <div className="hardskill-item" key={generateUniqueKey()}>
                            <FontAwesomeIcon
                              icon={getIconForHardSkill(hardskill)}
                              className="icon"
                            />
                            <span className="skill-text">{hardskill}</span>
                          </div>
                        ))}
                      </div>
                    </Col>
                  ))}
              </Row>
            </Col>
          </>
        )}
      </Row>
    </Container>
  );
};

HardSkills.propTypes = {
  hardSkills: PropTypes.objectOf(PropTypes.arrayOf(PropTypes.string)).isRequired,
};

export default HardSkills;
